.forms-call .get-buttons {
    font-size: .938rem;
    margin-top: 10px;
    padding: 10px 50px;
    transition: all .5s ease .1s;
    text-decoration: none;
    color: #fff;
    border: 0;
    border: 1px solid  #dc143c;
    background-color: #dc143c; }

.button-call {
    float: left;
    margin: 0;
    text-align: center; }

.forms-call .get-buttons:focus,
.forms-call .get-buttons:hover,
.forms-call .get-buttons:active {
    color: #0cb4ce;
    border: 1px solid #0cb4ce;
    background-color: transparent; }

/* конец формы обратной связи */
/*оформление всплывающей формы а ниже и благодарности */
.forms-calldecor {
    position: relative;
    overflow: hidden;
    max-width: 480px;
    margin: auto;
padding: 40px 10px;
    transition: all .5s ease .1s;
    border: 1px solid #fff;
    border-radius: 1rem;
    background-color: rgba(0, 0, 0, .5);
}

.forms-calldecor h4, .forms-calldecor h5 {
    font-size: 1.3rem;
    margin: 10px 20px 0;
    text-align: center;
    color: #fff; }

.forms-calldecor h5 {
    font-size: .96rem;
    margin-top: 10px;
    margin-bottom: 30px;
    letter-spacing: 1px; }

.forms-calldecor input {
    display: block;
    font-size: 1.0rem;
text-indent: 20px;
    width: 90%;
    padding: 5px 0;
    margin: 0 auto 10px;
border: 0;
 }

.forms-calldecor .button-call {
width: 100%;
text-align: center;
color: #0cb4ce;
 }
.forms-calldecor .button-call .get-buttons {
 width: 90%;
 background-color: transparent;
 border: 1px solid #fff;
 color: #fff;
padding: 12px 0 10px 0;
font-size: 1.0rem;
text-transform: uppercase;
line-height: 1;
transition: all .5s ease .1s;
cursor: pointer;
}
.forms-calldecor .button-call .get-buttons:hover {
color: #000000;
background-color: #0cb4ce;
}

.forms-calldecor .mfp-close {
    position: absolute;
    top: 15px;
    right: 15px;   color: #fff;
    border-radius: 3px; }

.forms-calldecor .success {
    position: absolute;
    z-index: 1;
    top: -100%;
    left: 0;
   display: flex;
    width: 100%;
    height: 100%;
    padding-right: 20px;
    padding-left: 20px;
    transition: all .5s ease .1s;
    opacity: 0;
    background-color: #000;
}

.forms-calldecor .success.active {
    top: 0;
    opacity: 1;
}

.forms-calldecor .success p {
    font-size: 20px;
   display: flex;
    margin: auto;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
}

/* overlay at start */
.mfp-fade.mfp-bg
{
 transition: all .615s ease-out;
  opacity: 0;
}
/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
    opacity: .8;
}

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
    opacity: 0;
}

/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
    opacity: 0;
}

/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
transition: all .615s ease-out;
transform: translateY(1%);
opacity: 1;
}

/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
transform: translateY(-120%);
opacity: 0;
}

